<template>
  <div>
    <!-- 오브젝트 형태로 여러개의 클래스를 처리할 수 있다. key값은 클래스명:true와 false를 사용,
    클래스명이 한단어일 경우 ''생략가능하다 -->
    <div :class="{ 'text-red': true, active: true }">클래스 바인딩</div>
    <div :class="{ 'text-red': hasError, active: isActive }">
      클래스 바인딩2
    </div>
    <div :class="class2">클래스 바인딩3</div>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      isActive: false,
      hasError: false,
      class2: ['active', 'hasError']
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {}
}
</script>

